<template>
	<b-modal ref="ConfigSyncProduct" centered title="Cấu hình" @ok="saveConfig">
		<div slot="modal-ok">
			OK
			<b-spinner v-show="waiting" small />
		</div>
		<b-form-group
			class="text-left"
			label="Thông tin KHÔNG được phép thay đổi khi cập nhật sản phẩm trên Kiotviet"
		>
			<b-form-checkbox
				v-for="option in options"
				v-model="selected"
				:key="option.value"
				:value="option.value"
				name="flavour-3a"
			>
				{{ option.text }}
			</b-form-checkbox>
		</b-form-group>
	</b-modal>
</template>
<script>
import { mapState } from "vuex";
import { AuthService } from "../../../services/AuthService";

export default {
	name: "ConfigSyncProduct",
	data() {
		return {
			waiting: false,
			selected: [], // Must be an array reference!
			options: [
				{ text: "Tên sản phẩm", value: 1 },
				{ text: "Danh mục sản phẩm", value: 2 },
				{ text: "Ảnh sản phẩm", value: 3 },
				{ text: "Mô tả / Ghi chú", value: 4 }
			]
		};
	},
	computed: {
		...mapState(["configKiotviet"])
	},
	watch: {
		configKiotviet(value) {
			if (value) {
				const productSync = value.product_sync.map(item => {
					return parseInt(item);
				});

				this.selected = productSync;
			}
		}
	},
	methods: {
		showModal() {
			this.$refs["ConfigSyncProduct"].show();
		},
		async saveConfig(evt) {
			evt.preventDefault();
			this.waiting = true;
			const request = {
				product_sync: this.selected
			};

			const response = await AuthService.saveConfig(request);
			this.waiting = false;
			if (response.status == "success") {
				this.$nextTick(() => {
					this.$refs["ConfigSyncProduct"].hide();
				});
			}
		}
	}
};
</script>



