<template>
	<b-col md="12" class="search-box">
		<img
			class="position-absolute"
			style="top: 5px"
			src="../../../assets/img/icon_search.png"
		/>
		<input
			ref="input"
			type="text"
			@input="searchProductByName"
			v-model="qProduct"
			placeholder="Tìm kiếm hàng hóa"
			class="input-search"
		/>
		<div class="line-bottom"></div>
		<div v-show="displayResult" class="result-search">
			<p
				v-show="!resultProductSearch.length"
				style="text-align:center; padding: 10px"
			>
				Không tìm thấy hàng hóa phù hợp
			</p>
			<ul>
				<li
					v-for="(item, index) in resultProductSearch"
					:key="index"
					class="item-search"
					@click="addProduct(item)"
				>
					<img
						v-if="typeof item.images !== 'undefined'"
						:src="item.images[0]"
					/>
					<img
						v-else
						src="data:image/png;base64,"
					/>
					<span>{{ item.fullName }}</span>
				</li>
			</ul>
		</div>
	</b-col>
</template>
<script>
import mixins from "../../../mixins";

export default {
	name: "ProductSearch",
	props: ["productKiotvietAll"],
	mixins: [mixins],
	data() {
		return {
			displayResult: false,
			resultProductSearch: [],
			qProduct: ""
		};
	},
	methods: {
		searchProductByName() {
			this.$parent.$parent.$refs.ProductTable.searchProductByName(
				this.qProduct
			);
		},
		focus() {
			this.$refs.input.focus();
		}
	}
};
</script>



