<template>
	<div>
		<b-form-group label="Chọn hàng hóa để đồng bộ">
			<b-form-checkbox-group
				stacked
				v-model="configProduct"
				:options="[
					{ text: 'Xóa các hàng hóa hiện có trên Website', value: 1 }
				]"
			>
			</b-form-checkbox-group>
			<b-form-checkbox-group
				stacked
				v-model="configProduct"
				:options="[
					{
						text:
							'Thêm mới hàng hóa trên KiotViet chưa tồn tại trên Website',
						value: 2
					}
				]"
			>
			</b-form-checkbox-group>
		</b-form-group>
	</div>
</template>
<script>
export default {
	name: "ProductHeader",
	data() {
		return {
			configProduct: []
		};
	}
};
</script>



