<template>
	<div class="step">
		<b-row class="justify-content-center branch">
			<b-col md="10">
				<b-table
					:fields="fields"
					:items="items"
					class="table-select branch"
				>
					<template slot="config_branch_value" slot-scope="data">
						<v-select
							v-if="data.item.status"
							placeholder="Chọn chi nhánh"
							label="branchName"
							v-model="config[data.index]"
							:options="branchKiotvietAll"
						>
							<template slot="no-options">
								Không có kết quả phù hợp
							</template>
						</v-select>

						<b-form-checkbox
							style="padding-bottom: 10px; padding-top: 5px"
							v-if="!data.item.status"
							switch
							@change="saveConfig"
							v-model="autoSyncOrder"
							>Tự động chuyển đơn đặt hàng về Kiotviet
							<img
								@mouseover="overDescription"
								@mouseleave="leaveDescription"
								class="ml-1"
								src="./../../../assets/img/icon_warning.png"
							/>
							<div v-show="displayDescription" class="description">
								<p>
									Đơn hàng sẽ không được đồng bộ tự động về KiotViet
									nếu bạn tắt tính năng này.<br />
									Vui lòng chọn <b>Đồng bộ lại</b> cho từng đơn hàng
									trong <b>Danh sách đơn đặt hàng</b> để đồng bộ thủ
									công
								</p>
							</div>
						</b-form-checkbox>
					</template>
				</b-table>
				<div class="step-wrapper text-right">
					<b-button size="md" @click="back">Quay lại</b-button>
					<b-button size="md" @click="saveAndNext" variant="success"
						>Tiếp tục</b-button
					>
					<b-button size="md" @click="skip" variant="danger">Hủy</b-button>
				</div>
			</b-col>
		</b-row>
	</div>
</template>
<script>
import Vue from "vue";
import { mapState, mapMutations } from "vuex";
import { router } from "../../../router";
import { indexDbService } from "../../../services/indexDbService";
import { BranchService } from "../../../services/BranchService";
import { AuthService } from "../../../services/AuthService";

export default {
	name: "ConfigBranch",
	data() {
		return {
			fields: [
				{
					key: "config_branch_description",
					thClass: "thead-table",
					thStyle: "display: none",
					tdClass: "td-branch"
				},
				{
					key: "config_branch_value",
					thClass: "thead-table",
					thStyle: "display: none",
					tdClass: "td-branch"
				}
			],
			items: [
				{
					config_branch_description: "Chi nhánh đồng bộ tồn kho hàng hóa",
					status: true
				},
				{
					config_branch_description: "Chi nhánh đồng bộ đơn đặt hàng",
					status: true
				},
				{
					config_branch_description: "",
					status: false
				}
			],
			configBranch: [],
			branchKiotvietAll: [],
			autoSyncOrder: false,
			config: [],
			displayDescription: false
		};
	},
	computed: {
		...mapState("Step", ["currentStep", "lastStep"]),
		...mapState(["processBarGetData", "configKiotviet"])
	},
	watch: {
		async processBarGetData(value) {
			if (Math.ceil(value) >= 100) {
				this.branchKiotvietAll = await indexDbService.getAll("branchs");
				const response = await BranchService.getConfig();
				this.configBranch = response.data.data;
				if (response.data.status == "success") {
					this.configBranchStock();
					this.configBranchOrder();
				}
			}
		},
		configKiotviet(value) {
			if (value) {
				this.autoSyncOrder = value.auto_sync_order ? true : false;
			}
		}
	},
	methods: {
		...mapMutations("Step", ["nextStep", "backStep", "setCurrentStep"]),
		...mapMutations("Branch", [
			"setConfigBranchStock",
			"setConfigBranchOrder"
		]),
		configBranchStock() {
			const indexBranchStock = this.branchKiotvietAll.findIndex(value => {
				if (this.configBranch.config_branch_stock) {
					return value.id == this.configBranch.config_branch_stock.id;
				}
			});

			if (indexBranchStock !== -1) {
				Vue.set(this.config, 0, this.branchKiotvietAll[indexBranchStock]);
				this.setConfigBranchStock(this.configBranch.config_branch_stock);
			} else {
				Vue.set(this.config, 0, this.branchKiotvietAll[0]);
			}
		},
		configBranchOrder() {
			const indexBranchOrder = this.branchKiotvietAll.findIndex(value => {
				if (this.configBranch.config_branch_order) {
					return value.id == this.configBranch.config_branch_order.id;
				}
			});

			if (indexBranchOrder !== -1) {
				Vue.set(this.config, 1, this.branchKiotvietAll[indexBranchOrder]);
				this.setConfigBranchOrder(this.configBranch.config_branch_order);
			} else {
				Vue.set(this.config, 1, this.branchKiotvietAll[0]);
			}
		},
		saveAndNext() {
			const validate = this.validate();
			if (!validate) {
				return;
			}
			this.setConfigBranchStock(this.config[0]);
			this.setConfigBranchOrder(this.config[1]);
			this.nextStep();
			if (this.currentStep == this.lastStep) {
				router.push({
					name: "Sync"
				});
			}
		},
		skip() {
			this.setCurrentStep(1);
		},
		back() {
			this.backStep();
		},
		validate() {
			if (!this.config[0]) {
				this.$toaster.error(
					"Bạn chưa chọn chi nhánh đồng bộ tồn kho hàng hóa"
				);
				return false;
			}

			if (!this.config[1]) {
				this.$toaster.error("Bạn chưa chọn chi nhánh đồng bộ đơn đặt hàng");
				return false;
			}

			return true;
		},
		async saveConfig() {
			const request = {
				auto_sync_order: !this.autoSyncOrder
			};

			await AuthService.saveConfig(request);
		},
		overDescription() {
			this.displayDescription = true;
		},
		leaveDescription() {
			this.displayDescription = false;
		}
	}
};
</script>



